<template>
	<view class="container flex-box flex-column">
		<view v-if="hasForm">
			<view class="sh-top flex-box flex-xy-center flex-column">
				<view class="title flex-box flex-xy-center ">
					商会入驻
				</view>
				<view class="label">马上加入全国商会交流圈</view>
			</view>
			<view class="form-panel flex-1">
				<!-- <input-item:name="item.name" :inputData="item" :inputType="item.type"></input-item> -->
				<view class="form-list bd-bottom" v-for="(item,index) in formList" :key="index">
					<div class="form-item flex-box flex-y-center">
						<view class="label">
							<text>{{item.label}}</text>
						</view>
						<view class="flex-1">
							<input v-if="item.type=='number'" v-model="item.val" :name="item.name" maxlength="11" :placeholder="'请输入'+item.label" />
							<input v-if="item.type=='text'" v-model="item.val" :name="item.name" :placeholder="'请输入'+item.label" />
						</view>
					</div>
				</view>
			</view>
			<!-- 流程图 -->
			<view class="step-wrap">
				<div class="title">商会入驻流程</div>
				<view class="flex-box flex-left-right-between step-list">
					<view class="icon-item">
						<image src="../../static/images/icon/computer.png" mode=""></image>
						<text>线上申请</text>
					</view>
					<view class="icon-item">
						<image src="../../static/images/icon/telephone.png" mode=""></image>
						<text>电话核实</text>
					</view>
					<view class="icon-item">
						<image src="../../static/images/icon/txt.png" mode=""></image>
						<text>商会上线</text>
					</view>
				</view>
			</view>
		</view>

		<view class="Audit" v-if="!hasForm">
			<view class="InAudit">
				<image src="../../static/ok.png" mode=""></image>
				<view class="title">入驻申请提交成功</view>
				<view class="label">
					稍后我们的客服将与您联系
				</view>
			</view>
		</view>



		<view class="fixed-bottom">
			<button class="yzkk-submitBtn joinNow" v-if="hasForm" hover-class="yzkk-btn-hover" @click="joinNow">提交申请</button>

			<!-- 已提交申请，等待电话核实 -->
			<button class="yzkk-submitBtn joinNow" v-if="!hasForm" hover-class="yzkk-btn-hover" @click="navBack()">知道了</button>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				hasForm: true,
				formList: [{
					label: '商会名称',
					val: '',
					name: 'chamber_name',
					type: 'text',
					length: 20
				}, {
					label: '姓名',
					val: '',
					name: 'real_name',
					type: 'text',
					length: 15
				}, {
					label: '手机号',
					val: '',
					name: 'phone',
					type: 'number',
					length: 11
				}, ]
			}
		},
		methods: {
			navBack() {
				this.$commonJs.navBack();
			},
			joinNow() {
				let Rules = [{
						name: 'chamber_name',
						type: 'required',
						errmsg: '请输入商会名称'
					},
					{
						name: 'real_name',
						type: 'required',
						errmsg: '请输入您的姓名'
					},
					{
						name: 'phone',
						type: 'required',
						errmsg: '请输入您的手机号'
					},
					{
						name: 'phone',
						type: 'phone',
						errmsg: '请输入正确的手机号'
					},
				];
				var formData = {};
				this.formList.forEach((item) => {
					formData[item.name] = item.val
				})
				let valLoginRes = this.$validate.validate(formData, Rules)
				if (!valLoginRes.isOk) {
					this.$commonJs.uniMsg(valLoginRes.errmsg)
					return false
				}
				// 入驻商会
				this.$apiJs.setChamberJoin(formData).then(res => {
					if (res) {
						this.hasForm = false;
					}
				})
			}
		},
		onLoad(option) {
			if(option.status){
				this.hasForm = false;
			}
		}

	}
</script>

<style lang="scss">
	.container {
		height: 75vh;
	}

	.sh-top {
		height: 200rpx;
		text-align: center;

		.title {
			font-size: 40rpx;
			font-weight: 800;
			flex: 1;
		}

		.label {
			flex: 1;
			font-size: 30rpx;
			color: #999999;
		}
	}

	.sh-bg {
		height: 358rpx;
		width: 92%;
		margin: 20rpx auto;
		background-image: url(../../static/images/joinBg.png);
		background-size: cover;

		.sh-bg-con {
			text-align: center;

			text {
				color: #FFFFFF;
				font-size: 40rpx;
				font-weight: 800;
			}
		}
	}

	.form-list {
		height: 88rpx;
		padding: 0rpx 30rpx;
		line-height: 88rpx;
		box-sizing: border-box;
		background-color: #fff;

		.label {
			min-width: 138rpx;

			text {
				font-size: 30rpx;
				font-weight: 800;

			}
		}

		input {
			text-align: right;
		}
	}


	.step-wrap {
		.title {
			margin: 60rpx auto;
			text-align: center;
		}

		.title,
		text {
			font-size: 28rpx;
			color: #999999;
		}

		.step-list {
			position: relative;

			.icon-item {
				display: flex;
				flex: 1;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				image {
					width: 80rpx;
					height: 80rpx;
					border: 1px solid #DDDDDD;
					border-radius: 50%;
					padding: 20rpx;
					box-sizing: border-box;
					margin-bottom: 10rpx;
				}
			}

			&::before {
				content: "";
				position: absolute;
				left: 28%;
				top: 33%;
				transform: translateY(-50%);
				width: 96rpx;
				height: 1rpx;
				background-color: #DDDDDD;
			}

			&::after {
				content: "";
				position: absolute;
				right: 28%;
				top: 33%;
				transform: translateY(-50%);
				width: 96rpx;
				height: 1rpx;
				background-color: #DDDDDD;
			}
		}

	}

	.Audit {
		text-align: center;

		image {
			width: 180rpx;
			height: 180rpx;
			margin-top: 280rpx;
		}

		image.shenhezhong {
			width: 475rpx;
			height: 261rpx;
			// position: relative;
			margin-top: 169rpx;
			margin-bottom: 71rpx;
		}

		.title {
			width: 82%;
			color: #E56361;
			font-size: 36rpx;
			margin: 0 auto;
			font-weight: 800;
		}

		.label {
			width: 82%;
			margin: 0 auto;
			margin-top: 30rpx;
			font-size: 28rpx;
			color: #A7A7A7;
			line-height: 42rpx;
		}
	}

	.joinNow {
		margin: 25rpx auto;
	}
</style>
